import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'defaultTextPipe'})
export class DefaultTextPipe implements PipeTransform {
    public transform(input: string): string {
        if (!input) {
            return 'Special Class';
        } else {
            return input
        }
    }
    
}


