import { InfiniteScroll } from './../../core/constants';
import { ElementRef } from '@angular/core';
import { ViewChild } from '@angular/core';
import { GalleryviewpopupComponent } from './../../galleryviewpopup/galleryviewpopup.component';
import { GlobalSubscriptionService } from './../../app/globalSubcription.service';
import { TrainersdetailpopupComponent } from './../../trainer/trainersdetailpopup/trainersdetailpopup.component';
import { EditclasspopupComponent } from './../../editclasspopup/editclasspopup.component';
import { ClassDetail } from './../../core/models/classListResponse';
import { ModelpopupLrComponent } from './../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { JoinclasspopupComponent } from './../../joinclasspopup/joinclasspopup.component';
import { Router, ActivatedRoute } from '@angular/router';
import { TrainerGymDetail } from './../../core/models/gymtrainerDetailResponse';
import { MediaResponseDtoList } from "../../core/models/mediaResponseList";
import { Subscription } from 'rxjs/Subscription';
import { LocalStorageService } from './../../core/service/local-storage.service';
import { Constants } from '../../core/constants';
import { HttpUserUtilsService } from '../../core/http_wrapper/http-user-utils.service';
import { Component, OnInit } from '@angular/core';
import { ImageResult, ResizeOptions } from "ng2-imageupload";
import { HttpUserAuthUtilsService } from "../../core/http_wrapper/http-userauth-utils.service";
import { HttpSuccesFailureResponse } from "../../core/http_wrapper/http_wrapper_response";
import {  GymViewData } from "../../core/models/gymDetailResponse";

import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import {  overlayConfigFactory } from 'angular2-modal';
import { GymgalleryComponent } from "../gymgallery/gymgallery.component";
import { ImagePopupComponent } from "../../imageCroppingPopup/imageCrop.component";
import { UserLogin } from "core/models/userloginresponse";
import { ConfirmpopupComponent } from '../../common/confirmpopup/confirmpopup.component';
import { UserSession } from 'core/sessions';
import { AdminDto } from 'core/models/userprofile';
import { BaseComponent } from 'app/base/base.component';
declare var $: any;


@Component({
  selector: 'app-mygymhome',
  templateUrl: './mygymhome.component.html',
  styleUrls: ['./mygymhome.component.css']
})
export class MygymhomeComponent extends BaseComponent implements OnInit, HttpSuccesFailureResponse {
  @ViewChild('myinput') inputOne: ElementRef;
  coverMainFile: File;
  coverFileList: File[] = [];
  profileMainFile: File;
  profileFileList: File[] = [];
  gymDetailresponse: GymViewData;
  trainerDetailResponse: TrainerGymDetail[];
  coverfilelength: boolean = false;
  src: string = "";
  profsrc: string = "";
  profilefilelength: boolean = false;
  MediaResponseDtoList: MediaResponseDtoList[] = [];
  gallerycount: number = 0;
  subscription: Subscription;
  mediauploadsuccess: boolean = false;
  gymSessionClass: ClassDetail[] = []
  userLoggedIn: boolean = false;
  listIsLaoding: boolean = false;
  resizeOptions: ResizeOptions = {
    resizeMaxHeight: 253,
    resizeMaxWidth: 1200
  };
  upcomingclasscount: any = "0";
  title: string = 'map';
  latitude: number;
  longitude: number;
  mapmarker: string = './assets/images/map_marker.png';
  zoom: number = 12;
  isLoadMoreData = false;
  listSize = 0;
  lastScrollDistance: number = 0;
  currentPage: number = 0;
  shareurl: any;
  gallery = null;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;
  channelName:any;
  userAgent:any;
  platformNameString:any;
  browserName:any;
  currency:any;
  subCategoryList: any=[]=[];
  public showAddSubactivity=true;
public isMasterGym:boolean=false;

  constructor(private globalSubscription: GlobalSubscriptionService, private _activatedRoute: ActivatedRoute, private router: Router, private globalSubscriptionService: GlobalSubscriptionService, private user_http: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService, private modal: Modal) {     
    super(http_userauth);
  }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();
    this.isMasterGym = LocalStorageService.isMasterGym();
    this.browserName = Constants.getBrowserName();
    this.userAgent = this.getMobileOperatingSystem();
    this.loadAdminDataIfLoggenIn();

    this.getSubCategoryData();
    if (LocalStorageService.getUserType() == 2) {
      this.router.navigate(['/'])

    }
    this.subscription = this.globalSubscriptionService.GymGalleryState
      .subscribe((media) => {
        this.MediaResponseDtoList = media;
        if (this.MediaResponseDtoList) {
            const that = this;
            setTimeout(() => {
                if (that.gallery == null) {
                    that.gallery = window["$"]('.gallery a').simpleLightbox();
                }
                else {
                    that.gallery.destroy();
                    that.gallery = window["$"]('.gallery a').simpleLightbox();
                }
            }, 100);
          this.gallerycount = this.MediaResponseDtoList.length;
        } else {
          this.MediaResponseDtoList = [];
          this.gallerycount = 0;
        }
      });

    this.getGymDetail()
    this.findtrainerSessionClasses();
    this._activatedRoute.params.subscribe(params => {
      if ((params["sessionId"] != undefined && params["sessionId"] != "")) {
        this.openSessionPopup(params["sessionId"]);
        window["shouldOpen"] = false;
      }
      });
      //this.showAddSubactivity=LocalStorageService.showSubActivity();

  }
  getGymDetail() {

    this.http_userauth.getGymNewViewDetail(LocalStorageService.getUserId(), this, true);
  }

  showSafetyPoints(){
    alert("yahoo")
  }

  navigatetocreate() {
    this.router.navigate(['class'])
  }
  redirectmap(lat, long) {
    let latlng = lat + "," + long;
    window.open(Constants.googlemapurl.concat(latlng), '_blank')
    // window.location.href=Constants.googlemapurl.concat(latlng);
  }
  findtrainerSessionClasses() {
    this.listIsLaoding = true;
    this.user_http.getgymUpcommingClasses(LocalStorageService.getUserId(), this.currentPage, this, true)
  }

  joinclasspopup(classdetail: ClassDetail) {
    if (this.isLoggedIn()) {
      return this.modal.open(JoinclasspopupComponent, overlayConfigFactory({ classdetail }, BSModalContext));
    }
    else {
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));

    }
  }
  copytext() {
    var $temp = $("<input>");
    $("body").append($temp);
    $temp.val($("#myinput").val()).select();
    document.execCommand("copy");
    $temp.remove();
  }

  refreshList() {
    this.lastScrollDistance=0
    this.isLoadMoreData=true;
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;  
    this.currentPage = 0;
    this.gymSessionClass = [];
    this.findtrainerSessionClasses();
  }
  editclasspopup() {
    return this.modal.open(EditclasspopupComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));
  }
  addZ(n) {
    return n < 10 ? '0' + n : '' + n;
  }
  openparticipantslist(gymSessionObj: ClassDetail) {
    let parent = this;
    return this.modal.open(EditclasspopupComponent, overlayConfigFactory({ gymSessionObj, parent }, BSModalContext))
  }

  openSessionPopup(sessionId: string) {
    this.http_userauth.getClassDetailList(sessionId, null, this, true);
  }

  getMobileOperatingSystem() {
    const userAgent = window.navigator.userAgent || window.navigator.vendor;

    // Windows Phone must come first because its UA also contains "Android"
    if (/windows phone/i.test(userAgent)) {
        return 'Windows Phone';
    }

    if (/android/i.test(userAgent)) {
        return 'Android';
    }

    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent)) {
        return 'iOS';
    }

    return 'web';
}

getPlatformNameString(platform){
  return Constants.getPlatformNameStringFunction(platform);
}

callLiveVideoTokenAPI(classSessionId,link){
  this.channelName = classSessionId;
  //call live video api and in it's success if token is found , redirect to the url

  if(link.length>0){
    window.open(link, "_blank");
  }
  else{
    if(this.browserName=='chrome' || this.browserName=='safari' || this.browserName=='firefox' || this.browserName=='opera'){
      this.userAgent=='web' ? this.http_userauth.getToken(classSessionId,LocalStorageService.getUserId(),this,true) : alert(Constants.alertVideoBroadcastingUser)
    }
    else{ 
      alert("For seamless experience please download Google Chrome browser. The browser you are using is currently not supported by "+UserSession.getDynamicElements(Constants.GymName));
    }
  }
}

  onSuccess(type: any, responsedata: any) {
    if(type==Constants.getSubCategoryApi){
      this.subCategoryList=responsedata.data;
     }
    if (type == Constants.GymDetail) {
      this.gymDetailresponse = responsedata
      this.shareurl = UserSession.getDynamicElements(Constants.ShareUrl) + 'gym/' + this.gymDetailresponse.id + '/' +
      this.gymDetailresponse.studioName.replace(/[(+{})]/g, '').replace(/ /g, '');
      this.src = this.gymDetailresponse.coverImage;
      this.profsrc = this.gymDetailresponse.profileImage;
      if (this.gymDetailresponse.mediaResponseDtoList) {
        this.MediaResponseDtoList = this.gymDetailresponse.mediaResponseDtoList;
        this.gallerycount = this.MediaResponseDtoList.length;
        const that = this;
        setTimeout(() => {
            if (that.gallery == null) {
                that.gallery = window["$"]('.gallery a').simpleLightbox();
            }
            else {
                that.gallery.destroy();
                that.gallery = window["$"]('.gallery a').simpleLightbox();
            }
        }, 100);
      } else {
        this.MediaResponseDtoList = [];
        this.gallerycount = 0;
      }

      this.latitude = +this.gymDetailresponse.latitude;
      this.longitude = +this.gymDetailresponse.longitude;
      let userData: UserLogin = LocalStorageService.getUser();
      userData.data.firstName = this.gymDetailresponse.firstName;
      LocalStorageService.setUser(userData);
      this.globalSubscription.userProfileUpdate();

      this._activatedRoute.params.subscribe(params => {
        if ((params["notificationId"] != undefined && params["notificationId"] != "") && window["shouldOpen"]) {
            this.opentrainerdetails(1);
            window["shouldOpen"] = false;
        }
      });
      if(this.gymDetailresponse.gymBranchResponseList&&this.gymDetailresponse.gymBranchResponseList.length>0){
     let trainerGymMappingList = [];
     this.gymDetailresponse.gymBranchResponseList.forEach(element => {
      let req = {branchId:element.branchId}
      trainerGymMappingList.push(req);
     });
     LocalStorageService.setTrainerGymMappingList(trainerGymMappingList);
    }
  }

    if(type === Constants.getTokenAPI) {
      if (responsedata.data.hasOwnProperty("token")) {
         LocalStorageService.setBroadCastToken(responsedata.data.token);
         LocalStorageService.setChannelName(this.channelName);
        this.router.navigate(['/videobroadcast']);
      }
    }

    if (type == Constants.trainerFilterListApi) {

      if (responsedata.data.hasOwnProperty("toTime")) {
        this.openparticipantslist(responsedata.data);
      }
      else {
        console.error("Gym details error.");
      }
    }

    if (type == Constants.TrainerUpcomingClassesApi) {
      this.listIsLaoding = false;
      var temp_list: ClassDetail[] = responsedata.data;
      
      if (this.currentPage == 0 && temp_list[0] != undefined && temp_list[0].count != undefined) {
        this.upcomingclasscount = temp_list[0].count;
      }
      var temp_length: number = this.gymSessionClass.length
      if(temp_list != undefined){
        temp_list.forEach(element => {
          this.gymSessionClass.push(element);
        });
      }
     

      this.listSize = this.gymSessionClass.length;
      if (temp_list == undefined || temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      } console.log(this.gymSessionClass)
    }
    if (type == Constants.mediaUpdate) {
      this.mediauploadsuccess = true;
    }
    if (type == Constants.FileUploadApi) {
    }
    if (type == Constants.FindTrainersByGym) {
      this.trainerDetailResponse = responsedata;
      //return this.modal.open(TrainersdetailpopupComponent,  overlayConfigFactory({trainerDetailResponse:this.trainerDetailResponse}, BSModalContext));


    }

  }
  openInfo() {
    let message = Constants.deletedClass;
    let title = "Class Deleted!";
    this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));
  }

  onFailure(type: any, response: any) {

    if (type == Constants.trainerFilterListApi) {
      let message = JSON.parse(response._body).errorMessage;
      if (message == Constants.deletedClass) {
        this.openInfo();
      }
    }

    if(type === Constants.getTokenAPI) {
      alert(response);
    }

    if (type == Constants.TrainerUpcomingClassesApi) {
      this.listIsLaoding = false;

    }

    if (type == Constants.mediaUpdate) {
      this.mediauploadsuccess = false;
    }
  }


  isLoggedIn() {
    return LocalStorageService.getUser()
  }
  routeToTrainerDetails(value: ClassDetail) {
    sessionStorage.setItem("trainer_id", value.trainerDetail.id + "");
    this.router.navigate(['/trainer', value.trainerDetail.id, value.trainerDetail.firstName]);
  }
  uploadPic(gymid: any, type: string) {
    const formData = new FormData();

    if (type == 'cover') {
      if (!this.coverFileList.length) return;
      Array.from(Array(this.coverFileList.length).keys()).map(x => {
        formData.append("media", this.coverFileList[x]);
        formData.append("mediaFor", "gymBackground");
        formData.append("mediaType", "Image");
        formData.append("objectDataId", gymid);
        formData.append("objectType", "gym");
      });
      if (this.gymDetailresponse.coverImage) {
        this.http_userauth.updateMedia(gymid, formData, this, true);
        if (!this.mediauploadsuccess) {
          return false;
        }
      }

    }
    if (type == 'profile') {
      if (!this.profileFileList.length) return;
      Array.from(Array(this.profileFileList.length).keys()).map(x => {
        formData.append("media", this.profileFileList[x]);
        formData.append("mediaFor", "GymProfile");
        formData.append("mediaType", "Image");
        formData.append("objectDataId", gymid);
        formData.append("objectType", "gym");
        formData.append("mediaName", "gymImage");
      });
      if (this.gymDetailresponse.profileImage) {
        this.http_userauth.updateMedia(gymid, formData, this, true);
        if (!this.mediauploadsuccess) {
          return false;
        }
      }
    }

    this.http_userauth.uploadMedia(formData, this, true);
  }
  handleImageLoad(src) {
    var img = new Image();
    img.src = src;
    img.onload = function () {
      if (img.width < 1000 && img.height < 200) {
        var trueImage = true;
      }
    };
  }
  coverImageSelected(imageResult: ImageResult) {
    if (this.coverMainFile) {
      let index = this.coverFileList.indexOf(this.coverMainFile);
      this.coverFileList.splice(index, 1);
    }

    this.coverMainFile = imageResult.file;
    this.coverFileList.push(this.coverMainFile);
    this.coverfilelength = false;
    this.openImageCropper(imageResult);
    this.uploadPic(LocalStorageService.getUserId(), 'cover')
  }
  profileImageSelected(imageResult: ImageResult) {
    if (this.profileMainFile) {
      let index = this.profileFileList.indexOf(this.profileMainFile);
      this.profileFileList.splice(index, 1);
    }
    this.profsrc = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.profileMainFile = imageResult.file;
    this.profileFileList.push(this.profileMainFile)
    this.profilefilelength = false;
    this.uploadPic(LocalStorageService.getUserId(), 'profile')
  }

  // for gallery
  opengallarey(MediaResponseDtoList) {
    return this.modal.open(GymgalleryComponent, overlayConfigFactory({ MediaResponseDtoList }, BSModalContext));
  }
  opentrainerdetails(num: number) {
    if (num > 0) {
      return this.modal.open(TrainersdetailpopupComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));
    }
  }
  routeToManageTrainer() {
    this.router.navigate(['/manageTrainer/']);
  
  }
  routeToManageAdmin() {
    this.router.navigate(['/manageAdmin/']);
  
  }
  openImageCropper(imageresult: ImageResult) {
    return this.modal.open(ImagePopupComponent, overlayConfigFactory({ imageresult }, BSModalContext));

  }
  gymeditprofile() {
    this.router.navigate(['gym/editprofile'])
  }

  onScroll() {


  }
  onScrollDown() {
    if (this.isLoadMoreData  && !this.listIsLaoding) {
      this.currentPage = this.currentPage + 1;
      this.listIsLaoding = true;
      this.user_http.getgymUpcommingClasses(LocalStorageService.getUserId(), this.currentPage, this, false);
    }
  }
  gallerryPopup(index) {
    let mediaList = this.MediaResponseDtoList;
    return this.modal.open(GalleryviewpopupComponent, overlayConfigFactory({ mediaList, index }, BSModalContext));

  }
  getSubCategoryData(){
    this.http_userauth.getAllSubCategory(this,true);
    }
  navigatetoAddSubActivity(){
    this.router.navigate(['listSubCategory'])

  }
  navigatetoEditSubActivity(id){
    this.router.navigate(['manageSubCategory/'+id])

  }
}



